import { newSpecPage } from '@stencil/core/testing';
import { PrestoCtaOverlayController } from '../presto-cta-overlay-controller';

describe('presto-cta-overlay-controller', () => {
  it('renders without overlay by default', async () => {
    const page = await newSpecPage({
      components: [PrestoCtaOverlayController],
      html: `<presto-cta-overlay></presto-cta-overlay>`,
    });
    expect(page.root).toEqualHtml(`
      <presto-cta-overlay></presto-cta-overlay>
    `);
  });
});
